@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color:lightgray;
}

body {
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#202c34;
  color:white;
  max-width: 1100px;
  margin:0 auto;
}



.App{text-align:left;padding:20px}.won-card{background-color:#4da375}.lost-card{background-color:#e15454}
.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}
.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}.MatchSmallCard .additional-details{text-align:right}.MatchSmallCard .additional-details>p{margin-bottom:20px}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:1.5rem}
.MatchPage{display:grid;grid-template-columns:1fr 4fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px auto}
.YearSelector{text-align:left;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}
.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .app-name{font-size:3rem;margin:30px auto}
.TeamTile{height:170px;text-align:center;border:1px solid #fff;padding:20px}
